	module.exports = {"data":{"site":{"siteMetadata":{"title":"Shang-Yu Su","subtitle":"Researcher of Natural Language Processing and Dialogue Systems.","copyright":"© All rights reserved.","author":{"name":"Shang-Yu Su","twitter":"#"},"disqusShortname":"","url":"https://www.shangyusu.com"}},"markdownRemark":{"id":"/Users/ShangYu/Desktop/github/shangyusu.github.io/src/pages/articles/IJCNLP2017/index.md absPath of file >>> MarkdownRemark","html":"<p><b>This paper is published in Proceedings of The 8th International Joint Conference on Natural Language Processing (IJCNLP 2017).</b></p>\n<p>Full paper:\n<a href=\"https://arxiv.org/abs/1710.00164\" target=\"_blank\">arXiv</a>,\n<a href=\"https://aclweb.org/anthology/I17-2028\" target=\"_blank\">ACL</a></p>\n<p>Code:\n<a href=\"https://github.com/MiuLab/Spk-Dialogue\" target=\"_blank\">GitHub</a></p>\n<p>Cite the paper:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">@inproceedings{chi2017speaker,\n  author    = {Ta-Chung Chi and Po-Chun Chen and Shang-Yu Su and Yun-Nung Chen},\n  title\t    = {Speaker Role Contextual Modeling for Language Understanding and Dialogue Policy Learning},\n  booktitle = {Proceedings of IJCNLP},\n  year\t    = {2017}\n}</code></pre>\n      </div>\n<p>Language understanding (LU) and dialogue policy learning are two essential components in conversational systems.\nHuman-human dialogues are not well-controlled and often random and unpredictable due to their own goals and speaking habits.</p>\n<p>This paper proposes a role-based contextual model to consider different speaker roles independently based on the various speaking patterns in the multi-turn dialogues.\nThe experiments on the benchmark dataset show that the proposed role-based model successfully learns role-specific behavioral patterns for contextual encoding and then significantly improves language understanding and dialogue policy learning tasks (The source code is available at: <a href=\"https://github.com/MiuLab/Spk-Dialogue\">https://github.com/MiuLab/Spk-Dialogue</a>).</p>","fields":{"tagSlugs":["/tags/dialogue/","/tags/nlu/"]},"frontmatter":{"title":"Speaker Role Contextual Modeling for Language Understanding and Dialogue Policy Learning","tags":["Dialogue","NLU"],"date":"2017-08-19T22:40:32.169Z","description":"Shang-Yu Su*, Ta-Chung Chi*, Po-Chun Chen*, and Yun-Nung Chen (co-first author)","conference":"IJCNLP 2017"}}},"pathContext":{"slug":"/publications/humane-typography-in-the-digital-age/"}}

